// BASIC
import React, { Component } from 'react'
import firebase, {db, st} from '../../../Config/firebase'
import styled, {css, keyframes} from 'styled-components'
import {FaPlus, FaTimes, FaMinus, FaImage, FaAngleLeft, FaAngleRight} from 'react-icons/fa'
// FUNCTIONS
import sortById from '../functions/sortById'

const Add = styled.div`
	position: absolute;
	bottom: -10px;
	left: 50%;
	transform: translateX(-50%);
	color: var(--color-main);
	opacity: 0;
	transition: all 0.2s ease;
	font-size: 18px;
	padding: 3px;
	background-color: var(--color-bg);
	border-radius: 100%;
	@media (hover: hover) {
		:hover {
			cursor: pointer;
		}
	}
	${props =>
		props.show === 'true' &&
		css`
			position: relative;
			opacity: 1;
		`
	};
`
const Delete = styled(FaTimes)`
	position: absolute;
	top: -10px;
	right: -10px;
	color: var(--color-main);
	opacity: 0;
	transition: all 0.2s ease;
	font-size: 25px;
	padding: 3px;
	background-color: var(--color-bg);
	border-radius: 100%;
	@media (hover: hover) {
		:hover {
			cursor: pointer;
		}
	}
`
const hideImage = keyframes`
	from {
		opacity: 1;
		transform: scale(1);
	}
	to {
		opacity: 0;
		transform: scale(1.1);
	}
`
const showImage = keyframes`
	from {
		opacity: 0;
		transform: scale(1.1);
	}
	to {
		opacity: 1;
		transform: scale(1);
	}
`
const Image = styled.div`
	position: relative;
	width: 100%;
	height: 100%;
	margin: 0 0 30px 0;
	background: url("${props => props.src}") no-repeat center center;
	background-size: cover;
	:hover ${Delete}, :hover ${Add} {
		opacity: 1;
	}
	animation: ${showImage} 0.3s both;
	${props =>
		props.hide === 'true' &&
		css`
			animation: ${hideImage} 0.3s both;
		`
	};
`
const Inputs = styled.div`
	position: relative;
	display: flex;
	flex-direction: column;
	width: 100%;
	max-width: 800px;
	:hover ${Delete}, :hover ${Add} {
		opacity: 1;
	}
`
const EditField = styled.div`
	display: flex;
	align-items: center;
`
const Label = styled.label`
	color: var(--color-main);
	margin: 5px;
	@media (hover: hover) {
		transition: all 0.2s ease;
		:hover {
			cursor: pointer;
			opacity: 0.7;
		}
	}
`
const InputWrapper = styled.div`
	width: 100%;
	position: relative;
	margin: 5px 0;
	margin-left: 10px;
`
const Progress = styled.div`
	position: absolute;
	top: 0;
	left: 0;
	width: ${props => props.width !== 100 ? `${props.width}%` : '0%'};
	height: 100%;
	background-color: var(--color-main);
	opacity: 0.2;
	z-index: -1;
	transition: width 0.3s ease;
`
const Input = styled.input`
	width: 100%;
	border: 2px solid var(--color-opacity);
	padding: 5px;
	font-size: 16px;
	:focus {
		border: 2px solid var(--color-main);
	}
	${props =>
		props.hide === 'true' &&
		css`
			display: none;
		`
	};
`
const SliderComponent = styled.div`
	position: relative;
	width: 90vw;
	max-width: 1000px;
	height: 300px;
	@media (min-width: 540px) {
		height: 400px;
		margin: 10px 0 30px 0;
	}
	overflow: hidden;
`
const Menu = styled.div`
	display: flex;
	align-items: center;
	position: absolute;
	bottom: 5px;
	left: 50%;
	transform: translateX(-50%);
`
const Circle = styled.div`
	width: 7px;
	height: 7px;
	margin: 5px;
	background-color: white;
	${props =>
		props.active === 'true' &&
		css`
			width: 10px;
			height: 10px;
			border-radius: 100%;
		`
	};
`
const LeftArrow = styled(FaAngleLeft)`
	position: absolute;
	left: 5px;
	top: 50%;
	transform: translateY(-50%);
	font-size: 35px;
	color: var(--color-bg);
	@media (hover: hover) {
		transition: all 0.2s ease;
		:hover {
			cursor: pointer;
			transform: translateY(-50%) scale(1.1);
		}
	}
`
const RightArrow = styled(FaAngleRight)`
	position: absolute;
	right: 5px;
	top: 50%;
	transform: translateY(-50%);
	font-size: 35px;
	color: var(--color-bg);
	@media (hover: hover) {
		transition: all 0.2s ease;
		:hover {
			cursor: pointer;
			transform: translateY(-50%) scale(1.1);
		}
	}
`

class Slider extends Component {
	state = {
		images: sortById(Object.values(this.props.images)),
		index: 0,
		hide: false,
		uploading: {
			element: null,
			progress: 0
		},
		ready: null
	}
	componentDidMount() {
		this.changeImage = setInterval(() => {
			this.setState({hide: true})
			setTimeout(() => {
				this.setState(prevState => ({
					index: prevState.index < prevState.images.length - 1 ? prevState.index + 1 : 0,
					hide: false
				}));
			}, 300)
		}, 7000);
	}
	componentWillUnmount() {
		if (!this.props.adminView) {
			clearInterval(this.changeImage)
		}
	}
	nextImage = () => {
		clearInterval(this.changeImage)
		this.setState({hide: true})
		setTimeout(() => {
			this.setState(prevState => ({
				index: prevState.index < prevState.images.length - 1 ? prevState.index + 1 : 0,
				hide: false
			}));
		}, 300)
		this.componentDidMount()
	}
	prevImage = () => {
		clearInterval(this.changeImage)
		this.setState({hide: true})
		setTimeout(() => {
			this.setState(prevState => ({
				index: prevState.index > 0 ? prevState.index - 1 : prevState.images.length - 1,
				hide: false
			}));
		}, 300)
		this.componentDidMount()
	}
	sendImage = (e) => {
		const id = e.target.id;
		const file = e.target.files[0];
		const storageRef = st.ref(`${this.props.page}/slider/${file.name}`);
		const task = storageRef.put(file);
		task.on('state_changed',
		(snap) => {
			this.setState({uploading: {
					element: id,
					progress: (snap.bytesTransferred / snap.totalBytes) * 100
				}
			})
		}, err => {
			console.log(err);
		}, () => {
			st.ref(`${this.props.page}/slider`).child(file.name).getDownloadURL().then(url => {
				console.log(url);
				let elementId = 0
				this.state.uploading.element.replace(/[0-9]+/, x => elementId = Number(x))
				db.collection('pages').doc(this.props.page).update({
					[`content.${this.props.name}.images.${id}`]: {
						id: elementId,
						src: url.replace(/\.[a-z]+\?alt=media.+/, link => `_2000x800${link}`)
					}
				}).then(() => {
					db.collection('pages').doc(this.props.page).get().then(snap => {
						this.setState({
							images: []
						}, () => this.setState({
							images: sortById(Object.values(snap.data().content[this.props.name].images)),
							uploading: {
								element: null,
								progres: 0
							}})
						)
					})
				})
			})
		})
	}
	addUrl = (e) => {
		let value
		console.log(e);
		if (typeof(e) === 'string') {
			value = document.getElementById(e).previousElementSibling.value
			if (/^https:\/\//.test(value)) {
				db.collection('pages').doc(this.props.page).update({
					[`content.${this.props.name}.images.${e}.src`]: value
				}).then(() => {
					db.collection('pages').doc(this.props.page).get().then(snap => {
						this.setState({
							images: sortById(Object.values(snap.data().content[this.props.name].images)),
							ready: null
						})
					})
				})
			}
			else {
				alert("Adres musi zaczynać się protokołem \"https://\"!")
			}
		}
		else {
			value = document.getElementById('addUrl').value
			if (/^https:\/\//.test(value)) {
				let id = `src_${(this.state.images.length + 1).toString().padStart(2, '0')}`
				db.collection('pages').doc(this.props.page).update({
					[`content.${this.props.name}.images.${id}`]: {
						id: this.state.images.length + 1,
						src: value
					}
				}).then(() => {
					db.collection('pages').doc(this.props.page).get().then(snap => {
						this.setState({
							images: sortById(Object.values(snap.data().content[this.props.name].images)),
							ready: null
						})
					})
				})
			}
			else {
				alert("Adres musi zaczynać się protokołem \"https://\"!")
			}
		}
	}
	deleteImage = (id) => {
		if (window.confirm("Na pewno chcesz usunąć zdjęcie?")) {
			db.collection('pages').doc(this.props.page).update({
				[`content.${this.props.name}.images.${id}`]: firebase.firestore.FieldValue.delete()
			}).then(() => {
				db.collection('pages').doc(this.props.page).get().then(snap => {
					this.setState({
						images: sortById(Object.values(snap.data().content[this.props.name].images)),
						ready: null
					})
				})
			})
		}
	}
	setReady = (e) => {
		if (e.target.value && e.target.value !== e.target.defaultValue) {
			this.setState({ready: e.target.nextSibling.id})
		}
		else {
			this.setState({ready: null})
		}
	}
	render() {
		return (
			<>
				{this.props.adminView ? <Inputs>
					<Delete onClick={() => this.props.delete(this.props.name)} />
					{this.props.last && <Add onClick={() => this.props.add(this.props.index)}>
						<FaPlus />
					</Add>}
					{this.state.images.length === 0 ? '' : this.state.images.map(image => {
						const id = `src_${(image.id).toString().padStart(2, '0')}`
						return <EditField key={`image_${image.id}`}>
							{this.state.ready === id && <Label onClick={() => this.addUrl(id)}><FaPlus /></Label>}
							<Label onClick={() => this.deleteImage(id)}><FaMinus /></Label>
							<Label><FaImage onClick={() => document.getElementById(id).click()} /></Label>
							<InputWrapper>
								<Progress width={this.state.uploading.element === id ? this.state.uploading.progress : 0} />
								<Input onChange={this.setReady} type="text" defaultValue={image.src} />
								<Input onChange={this.sendImage} id={id} hide="true" type="file" />
							</InputWrapper>
						</EditField>
					})}
					<EditField>
							{this.state.ready === `src_${(this.state.images.length + 1).toString().padStart(2, '0')}` && <Label onClick={this.addUrl}><FaPlus /></Label>}
							<Label><FaImage onClick={() => document.getElementById(`src_${(this.state.images.length + 1).toString().padStart(2, '0')}`).click()} /></Label>
							<InputWrapper>
								<Progress width={this.state.uploading.element === `src_${(this.state.images.length + 1).toString().padStart(2, '0')}` ? this.state.uploading.progress : 0} />
								<Input onChange={this.setReady} id="addUrl" type="text" defaultValue="" />
								<Input onChange={this.sendImage} id={`src_${(this.state.images.length + 1).toString().padStart(2, '0')}`} hide="true" type="file" />
							</InputWrapper>
						</EditField>
				</Inputs> : <SliderComponent>
					{this.state.images.map((img, i) => {
						return <>
							{this.state.index === i && <Image hide={this.state.hide.toString()} src={img.src} />}
						</>
					})}
					<LeftArrow onClick={this.prevImage} />
					<RightArrow onClick={this.nextImage} />
					<Menu>
						{this.state.images.map((img, i) => {
							return <Circle active={(this.state.index === i).toString()} />
						})}
					</Menu>
				</SliderComponent>
				}
			</>
		);
	}
}

export default Slider