// BASIC
import React, { Component } from 'react'
import styled from 'styled-components'
import {au, fu} from '../../Config/firebase'

const LoginComponent = styled.div`
	min-height: calc(100vh - 421.4px);
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	padding: 0 30px;
	text-align: center;
`
const Form = styled.form`
	width: 90vw;
	display: flex;
	flex-direction: column;
	justify-content: center;
	@media (min-width: 900px) {
		width: 500px;
	}
`
const Title = styled.h1`
	margin: 20px 0;
	font-size: 30px;
	::first-letter {
		color: var(--color-main);
	}
`
const Input = styled.input`
	font-size: 16px;
	border: 2px solid var(--color-main);
	margin: 5px 0;
	padding: 10px;
	font-family: 'Ubuntu', serif;
	:focus {
		::placeholder {
			transition: all 0.2s ease;
			opacity: 0;
			transform: translateX(10px)
		}
	}
`
const Submit = styled.button`
	width: 100%;
	display: block;
	grid-column: 1/3;
	background-color: var(--color-main);
	color: var(--color-bg);
	font-size: 16px;
	margin: 20px 0;
	padding: 10px;
	@media (hover: hover) {
		transition: all 0.2s ease;
		:hover {
			cursor: pointer;
			opacity: 0.7;
		}
	}
`
const Profile = styled.div`
	max-width: 1000px;
	display: flex;
	flex-direction: column;
	align-items: center;
`
const Email = styled.h3`
	margin: 0;
	::first-letter {
		color: var(--color-main);
	}
`
const Info = styled.p`

`
const AddUsers = styled.div`
	display: flex;
`
const Add = styled.form`
	margin: 0 10px;
`

class Login extends Component {
	state = {
		userId: null,
		userEmail: null,
		isAdmin: false
	}
	componentDidMount() {
		au.onAuthStateChanged(user => {
			if (user) {
				this.setState({
					userId: user.uid,
					userEmail: user.email
				})
				user.getIdTokenResult().then(idTokenResult => {
					if (idTokenResult.claims.admin) {
						this.setState({isAdmin: true})
					}
				})
			}
			else {
				this.setState({
					userId: null,
					isAdmin: false
				})
			}
		})
	}
	logIn = async(e) => {
		const {email, password} = e.target.elements
		// console.log(email.value, password.value);
		try {
			await au.signInWithEmailAndPassword(email.value, password.value).catch(error => {
				alert(error.message);
			});
		}
		catch (error) {
			alert(error)
		}
	}
	logOut = async () => {
		console.log('log out');
		try {
			await au.signOut();
		}
		catch (error) {
			alert(error)
		}
	}
	addAdmin = (e) => {
		e.preventDefault();
		// const adminEmail = document.getElementById('adminEmail').value;
		const adminEmail = 'biuro@kuc-meble.com.pl';
		const addAdminRole = fu.httpsCallable('addAdminRole');
		addAdminRole(adminEmail).then(result => {
			console.log(result);
		})
	}
	render() {
		return (
			<LoginComponent>
				{!this.state.userId ? <Form onSubmit={this.logIn}>
					<Title>Zaloguj się</Title>
					<Input type="email" name="email" placeholder="Email" />
					<Input type="password" name="password" placeholder="Hasło" />
					<Submit type="submit">Zatwierdź</Submit>
				</Form> : <Profile>
					<Email>{this.state.userEmail}</Email>
					{this.state.isAdmin ? <>
						<Info>Jesteś administratorem! Przejdź do wybranej podstrony, aby ją edytować</Info>
						<AddUsers>
							<Add>
								<Input type="email" name="addAdmin" placeholder="Email" />
								<Submit type="submit" onClick={this.addAdmin}>Dodaj użytkownika</Submit>
							</Add>
							<Add>
								<Input type="email" name="addAdmin" placeholder="Email" />
								<Submit type="submit" onClick={this.addAdmin}>Dodaj administratora</Submit>
							</Add>
						</AddUsers>
					</> : <Info>Nie posiadasz praw administratora! skontaktuj się z nim, aby móc edytować stronę</Info>}
					<Submit onClick={this.logOut}>Wyloguj się</Submit>
				</Profile>}
			</LoginComponent>
		);
	}
}

export default Login