// BASIC
import React, { Component } from 'react'
import styled from 'styled-components'

const ListComponent = styled.div`
	width: 100%;
	margin-bottom: 30px;
	/* max-width: 800px; */
`
const Title = styled.p`
	font-size: 18px;
	margin: 20px 0;
	text-align: center;
`
const ListItems = styled.ul`
	list-style: none;
	text-align: left;
	padding-left: 5%;
`
const ListItem = styled.li`
	width: 100%;
	display: flex;
	margin: 5px 0;
	::before {
		content: '●';
		display: block;
		color: var(--color-main);
		/* font-weight: bold; */
		margin-right: 10px;
	}
`

class List extends Component {
	render() {
		return (
			<ListComponent>
				<Title>{this.props.title}</Title>
				<ListItems>
					{this.props.items.map((item, index) => <ListItem key={`item_${index}`}>{item}</ListItem>)}
				</ListItems>
			</ListComponent>
		);
	}
}

export default List