// BASIC
import React, { Component } from 'react'
import styled, {css, keyframes} from 'styled-components'
// ICONS
import {FaReply} from 'react-icons/fa'

const FoldersComponent = styled.div`
	width: 100%;
	display: flex;
	justify-content: center;
	flex-wrap: wrap;
`
const Folder = styled.div`

`
const Image = styled.div`
	position: relative;
	width: 200px;
	height: 200px;
	margin: 20px;
	border-radius: 20px;
	overflow: hidden;
	::before {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-color: var(--color-main);
		background: url("${props => props.bg}") no-repeat center center;
		background-size: cover;
		transition: all 0.3s ease;
		z-index: -1;
	}
	@media (hover: hover) {
		:hover {
			cursor: pointer;
			::before {
				transform: scale(1.1);
				opacity: 0.7;
			}
		}
	}
`
const Title = styled.div`
	font-weight: bold;
	font-size: 20px;
	::first-letter {
		color: var(--color-main)
	}
`
const fade = keyframes`
	from {
		opacity: 0;
		transform: scale(0.5)
	}
	to {
		opacity: 1;
		transform: scale(1)
	}
`
const Gallery = styled.div`
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	display: none;
	flex-direction: column;
	justify-content: space-between;
	align-items: center;
	padding: 30px;
	background-color: var(--color-bg);
	z-index: 2;
	animation: ${fade} 0.3s ease;
	${props =>
		props.show === 'true' &&
		css`
			display: flex;
		`
	}
`
const Images = styled.div`
	width: 100%;
	max-height: calc(100% - 50px);
	display: flex;
	justify-content: center;
	flex-wrap: wrap;
	overflow-y: scroll;

`
const Back = styled(FaReply)`
	color: var(--color-main);
	font-size: 25px;
	@media (hover: hover) {
		transition: all 0.1s ease;
		:hover {
			cursor: pointer;
			transform: scale(1.1);
		}
	}
`

class Folders extends Component {
	state = {
		showGallery: false,
		images: []
	}
	changeGalleryView = (images) => {
		console.log(images)
		this.setState(prevState => ({
			showGallery: !prevState.showGallery,
			images: Object.values(images)
		}));
	}
	render() {
		console.log(this.props.content);
		return (
			<FoldersComponent>
				<Gallery show={this.state.showGallery.toString()}>
					<Images>
						{this.state.images.map((image) => <Image bg={image} />)}
					</Images>
					{/* <Image bg={this.state.images[0]} />) */}
					<Back onClick={() => this.changeGalleryView([])} />
				</Gallery>
				{Object.values(this.props.content).map(({title, images}) => <Folder onClick={() => this.changeGalleryView(images)}>
					<Image bg={images.image_01} />
					<Title>{title}</Title>
				</Folder>)}
			</FoldersComponent>
		);
	}
}

export default Folders